import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'

import { Link } from 'gatsby'
import { PostFrontmatterType } from 'types/PostItem.types'
import { motion } from 'framer-motion'
type PostItemProps = PostFrontmatterType & { link: string }


const PostItemWrapper = styled(Link)`
  width: 100%;

  display: flex;
  flex-direction: column;
  border-radius: 15px 15px 0 0;
  transition: 0.3s box-shadow;
  
  cursor: pointer;
  transform: translate3d(0,0,0);
  position: relative;
  overflow: hidden;
 
  @media (max-width: 768px) {
    margin-bottom: 10px; 
  }

  
`

const AniBox = styled(motion.div)`
  
  overflow: hidden;
  margin-bottom: 40px;
  @media (max-width: 1200px) {
    margin-bottom: 20px; 
  }
`
const ThumbContainer = styled.img`
    width: 100%; 
    max-height: 400px; 
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-radius: 10px; */
    overflow: hidden;
    position: relative; 
    background-size: cover;
    background-position: center center;
    background-repeat: none;
    margin: 0;
`

const Content = styled.div`
    /* background: #ffffff;  */
    width: 100%; 
    height: 100%;
    padding: 28px 30px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    border-radius: 0 0 15px 15px;
    /* position: absolute;
    left: 0; top: 0;  */
    //background: #f1f1f0;
    background: rgba(241,241,241,.95);
`

const Date = styled.span`
    font-size: 1rem;
    line-height: .875em ;
    color: #999999;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    display: inline-block;
    margin-bottom: 10px; 
`
const Title = styled.h2`
    font-size: 1.25rem;
    line-height: 1.213em;
    letter-spacing: -0.04em;
    margin-bottom: 20px;
    @media (max-width: 640px) {
      height: auto;
      margin-bottom: 30px;
    }
`
const LabelWrap = styled.div`
    width: 100%; 
    display: flex;
    flex-grow: 1;
    align-items: flex-start;
    flex-wrap: wrap;
`
const Label = styled.span`
    padding: 3px 5px;
    /* border: 1px solid #DEDEDE; */
    border: 1px solid #3dd065;
    background: #3dd065; 
    color: #333333;
    border-radius: 4px;
    margin: 3px;
    display: inline-block;
    margin-right: 3px;
    font-size: .75rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
`
const box = {
  start: { scale: 1},
  hover: { scale: 1.1,}
};
const PostItem: FunctionComponent<PostItemProps> = function ({
    title,
    startDate,
    endDate,
    skills,
    thumbnail:{publicURL},
    link,
  }) {
    
    return (
      <AniBox
      variants={box}
      initial="start"
      whileHover="hover" 
      transition={{
        type: "tween",
        duration: "0.2"
      }}
    >
        <PostItemWrapper to={link}>
        {/* <PostItemWrapper ={onClickToggleModal} > */}
       
            <ThumbContainer src={publicURL} />
           
            <Content>
                <div>
                  <Date>{startDate} ~ {endDate}</Date>
                  <Title>{title}</Title>
                </div>
                <div>
                  <LabelWrap>{skills.map( skill => (
                      <Label>{skill}</Label>
                  ))}</LabelWrap>
                </div>
            </Content>
        
        </PostItemWrapper>
        </AniBox>
    )
  }
  
  export default PostItem

